export default {
    // tipo de ingreso
    updateTipoDeIngresoSeleccionado(state, tipoDeIngresoSeleccionado) {
        state.tipoDeIngresoSeleccionado = tipoDeIngresoSeleccionado;
    },
    // tipo de apoyo
    updateTipoDeApoyoSeleccionado(state, tipoDeApoyoSeleccionado) {
        state.tipoDeApoyoSeleccionado = tipoDeApoyoSeleccionado;
    },
    updateMontoDeApoyo(state, montoDeApoyo) {
        state.montoDeApoyo = montoDeApoyo;
    },
    // ingreso mensual
    updateIngresoMensual(state, ingresoMensual) {
        state.ingresoMensual = ingresoMensual;
    },
    // valor del inmueble
    updateValorDelInmueble(state, valorDelInmueble) {
        state.valorDelInmueble = valorDelInmueble;
    },
    // gastos mensuales
    updateGastosMensuales(state, gastosMensuales) {
        state.gastosMensuales = gastosMensuales;
    },
    // tipo de pago
    updateTipoDePago(state, tipoDePago) {
        state.tipoDePago = tipoDePago;
    },
    // plazoSeleccionado
    updatePlazoSeleccionado(state, plazoSeleccionado) {
        state.plazoSeleccionado = plazoSeleccionado;
    },
    // monto de credito
    updateMontoDeCredito(state, montoDeCredito) {
        state.montoDeCredito = montoDeCredito;
    },
    // modal
    updateModal(state, modal) {
        state.modal = modal;
    },
    updateActiveBank(state, data) {
        state.activeBank = data;
    }
}